import { FC, PropsWithChildren, forwardRef } from 'react';
import { styled } from '../../utils';
import { TwBaseProps } from '../../types';
import {
  DefaultProps,
  MultiSelect as MantineMultiSelect,
  MultiSelectProps as MantineMultiSelectProps,
} from '@mantine/core';

export interface MultiSelectProps
  extends TwBaseProps,
    PropsWithChildren,
    Omit<MantineMultiSelectProps, keyof Omit<DefaultProps, 'styles'>> {}

const Root = styled(MantineMultiSelect)<MantineMultiSelectProps>(() => ({}));

export const MultiSelect: FC<MultiSelectProps> = forwardRef((props, ref) => {
  return <Root {...props} ref={ref} />;
});
